import React, { useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { mfaVerify, sendMfaCode } from "../../actions/loginActions";
import { useNavigate } from "react-router-dom";
import { REMOVE_COOKIE, TOAST_ERROR } from "../../utils";

function MFALogin({ userData, mfaCollabratorToken = '' }) {
    const [mfaCode, setMfaCode] = useState('');
    const [useMfaCode, setUseMfaCode] = useState(false);
    const [spinner, setSpinner] = useState(false);
    const dispatch = useDispatch();
    const history = useNavigate();

    const handleSubmit = () => {
        setSpinner(true)
        if (mfaCode && mfaCode.length > 0) {
            if (!useMfaCode && (!/^\d{6}$/.test(mfaCode))) {
                TOAST_ERROR('Please enter a valid MFA Code');
            } else if (useMfaCode && mfaCode.length < 25) TOAST_ERROR('Please enter a valid Backup Code');
            else {
                const payload = { otp: mfaCode, mfa_token: mfaCollabratorToken || localStorage.getItem('mfa_token') || '' };
                dispatch(mfaVerify(payload, userData, history));
            }
            setSpinner(false);
        }
    }

    const sendMailMfaCode = () => sendMfaCode({ mfa_token: localStorage.getItem('mfa_token') || '' });

    useEffect(() => {
        const mfaToken = localStorage.getItem('mfa_token');
        if (!mfaToken || !userData?.token) {
            localStorage.clear();
            window.location.reload();
            REMOVE_COOKIE('mfa_users')
        }
    }, [history]);

    return (
        <div className="loginFormSec formControlMin mfaLogin">
            <div className="mfaBlock py-5">
                <div className="mfaHead mb-5">
                    <h3 className="display-2 fw-bold mb-5">Additional verification required</h3>
                    <p className="mb-2">Your account is protected with <strong className="text-dark">Multi-Factor Authentication (MFA).</strong></p>
                    <p>To complete the Sign In Process, please enter the code generated by your authenticator app below.</p>
                </div>
                <div className="">
                    <div className="form-group labelPosition mb-2">
                        <label className="fw-bold form-label" htmlFor="AccountEmailId">{useMfaCode ? 'Backup' : 'MFA'} Code</label>
                        <input name="mfaCode" autoComplete={'off'} className={`mb-1 form-control form-control-lg`} placeholder={`Enter ${useMfaCode ? 'Backup' : 'MFA'} code`} type="text" id="mfaCode" onChange={e => setMfaCode(e.target.value)} value={mfaCode} maxLength={50} />

                    </div>

                    <div className="form-group f-between mb-5 d-flex flex-wrap">
                        <p className="text-muted text-center fs-8 mb-0">{useMfaCode ? 'Backup' : 'MFA'}  Code not working? <Button variant="" className="p-0 text-dark border-0 btnSignUp link-active" onClick={() => { setUseMfaCode(!useMfaCode); setMfaCode('') }}>{useMfaCode ? 'Use MFA Code' : 'Use Backup Code'}</Button></p>
                        {useMfaCode ? <p className="mb-0"><Button variant="" className="p-0 text-dark border-0 btnSignUp link-active" onClick={sendMailMfaCode}>Still Having Troubles?</Button></p> : null}
                    </div>

                    <div className="form-group pt-1">
                        <Button variant="primary" className={`w-100`} data-style="expand-left" disabled={mfaCode.length < 6 || spinner} onClick={handleSubmit}>
                            {spinner ? <span className="spinner-border spinner-border-sm me-2"></span> : null}
                            Submit
                        </Button>
                    </div>

                </div>
            </div>
        </div>
    )
}

export default MFALogin;